*
{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Poppins', sans-serif;
}
.app
{
    background: #275950;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    text-align: center;
    width: 100%;
    height: 100%;
    min-height: 100vh;
    min-width: 100vw;
}
.imgapp
{
    width: 300px;
    filter: drop-shadow(8px 8px 4px black);
}